














































export default {
	data() {
		return {
			hall_list:[],
			hall_profit:[]
		};
	},
	onLoad:function(){
		var token=(uni.getStorageSync("user_info")).token;
		uni.request({
			url: 'http://bus.liebianzhe.com/api/user/get_user_profit',
			method: 'POST',
			data: {
				token:token
			},
			success: res => {
				console.info(res);
				if(res.data.code==1){
					this.hall_profit=res.data.data;
				}else if(res.data.code==10){
					uni.removeStorage({
						key:'user_info',
					})
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
					setTimeout(function(){
						uni.reLaunch({
							url:'../login/login'
						})
					},1500)
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1500
					});
				}
			},
			fail: () => {
				console.info("请求失败~");
			},
		});
		uni.request({
			url: 'http://bus.liebianzhe.com/api/user/tran_lists',
			method: 'POST',
			data: {
				token:token
			},
			success: res => {
				console.info(res);
				if(res.data.code==1){
					this.hall_list=res.data.data;
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1500
					});
				}
			},
			fail: () => {
				console.info("请求失败~");
			},
		});
	},
	methods:{
		jump_adver(){
			uni.navigateTo({
				url:'../adver_income/adver_income'
			})
		},
		jump_tc(){
			uni.navigateTo({
				url:'../tongcheng_income/tongcheng_income'
			})
		},
		jump_jf(){
			uni.navigateTo({
				url:'../jifen_income/jifen_income'
			})
		},
		jump_tg(){
			uni.navigateTo({
				url:'../tuiguang_income/tuiguang_income'
			})
		}
	}
}
